body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.result {
  height: 60px;
  background-color: #bbb;
  width: 100%;
}

.result p {
  font-size: 40px;
  margin: 5px;

}

.calculator-body {
  width: 100%;
  margin: auto;
}

.button {
  display: block;
  background-color: #bbb;
  margin: 0 auto;
  margin-top: 20px;
  height: 420px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.resultComponent{
  background-color:#fff;
  width: 450px;
  height:50px;
  color: black;
  border-radius: 15px;
  margin: 0 auto;
  font-size: 20px;
  font-weight: bolder;
  font-family: Arial, Helvetica, sans-serif;
  text-align: right;
  padding:10px;
  
}

button {
  width: 20%;
  height: 60px;
  font-size: 15px;
  margin-bottom:10px ;
  margin-left: 20px;
  border-radius: 5px;
  font-weight: bold;
}

